import React, { useState } from 'react';
import './Header.scss'
import headerLogo from '../../assets/header-logo.jpeg'
import { Link } from 'react-scroll';
import ScrollIndicator from './components/ScrollIndicator';

function Header() {

  const [showMenu, setShowMenu] = useState(false);
  const [isChecked, setIsChecked] = useState(false);

  const handleMenuToggle = () => {
    setShowMenu((prevShowMenu) => !prevShowMenu);
    setIsChecked((prevIsChecked) => !prevIsChecked);
  };

  return (
    <header className="header">
      <div className="header__container">
        <div className="header__logo-block">
          <img className="header__logo" src={headerLogo}></img>
        </div>
    
        <div className="header__right-block">
          <nav className="header__nav-list">
            <Link to="hero" spy={true} smooth={true} duration={500} offset={0} className="header__nav-item" >Главная</Link>
            <Link to="about" spy={true} smooth={true} duration={500} offset={-35} className="header__nav-item" >О Тренинге</Link>
            <Link to="reasons" spy={true} smooth={true} duration={500} offset={-20} className="header__nav-item" >Преимущества</Link>
            <Link to="speakers" spy={true} smooth={true} duration={500} offset={-20} className="header__nav-item" >Тренеры</Link>
            <Link to="schedule" spy={true} smooth={true} duration={500} offset={-20} className="header__nav-item" >Программа</Link>
            <Link to="prices" spy={true} smooth={true} duration={500} offset={-20} className="header__nav-item" >Стоимость</Link>
            <Link to="location" spy={true} smooth={true} duration={500} offset={-20} className="header__nav-item" >Локация</Link>
            <Link to="faq" spy={true} smooth={true} duration={500} offset={-50} className="header__nav-item" >FAQ</Link>
          </nav>
      
          <button className="header__contact-button">
            <Link to="location" spy={true} smooth={true} duration={500} offset={0}>Контакты</Link>
          </button>

          <div className='burger-button' >
            <label for="check" className={`burger-button__label ${isChecked ? 'checked' : ''}`}>
                <input type="checkbox" id="check" onClick={handleMenuToggle} checked={isChecked}/> 
                <span className='burger-button__burger-line'></span>
                <span className='burger-button__burger-line'></span>
                <span className='burger-button__burger-line'></span>
              </label>
          </div>
     
        </div>
      </div>
      {showMenu && 
        <div className={`menu-content ${showMenu ? 'show' : ''}`}>
          <div className='menu-content__container'>
            <div className='menu-content__nav-list'>
              <Link to="hero" spy={true} smooth={true} duration={500} offset={10} className='menu-content__nav-item' onClick={handleMenuToggle}>Главная</Link>
              <Link to="about" spy={true} smooth={true} duration={500} offset={0} className='menu-content__nav-item' onClick={handleMenuToggle}>О Тренинге</Link>
              <Link to="reasons" spy={true} smooth={true} duration={500} offset={0} className='menu-content__nav-item' onClick={handleMenuToggle}>Преимущества</Link>
              <Link to="speakers" spy={true} smooth={true} duration={500} offset={0} className='menu-content__nav-item' onClick={handleMenuToggle}>Спикеры</Link>
              <Link to="schedule" spy={true} smooth={true} duration={500} offset={0} className='menu-content__nav-item' onClick={handleMenuToggle}>Программа</Link>
              <Link to="prices" spy={true} smooth={true} duration={500} offset={0} className='menu-content__nav-item' onClick={handleMenuToggle}>Стоимость</Link>
              <Link to="location" spy={true} smooth={true} duration={500} offset={-25} className='menu-content__nav-item' onClick={handleMenuToggle}>Локация</Link>
              <Link to="faq" spy={true} smooth={true} duration={500} offset={0} className='menu-content__nav-item' onClick={handleMenuToggle}>FAQ</Link>

              <button className="menu-content__contacts-button">
                <Link to="location" spy={true} smooth={true} duration={500} offset={-25} onClick={handleMenuToggle}>Контакты</Link>
              </button>
            </div>
          </div>
        </div>}
    </header>
    
  );
}

export default Header;
