import React from "react";
import './Schedule.scss'
import { Fade } from "react-reveal";
import pdf from '../../documets/programm2.pdf'


function Schedule({ scheduleData }) {

  const handleInstrClick = () => {
    window.open(pdf, '_blank');
  };

  return (
    <section className="schedule-section">
      <h1 className="schedule-section__main-title">Программа обучения</h1>
      <span className="schedule-section__date-range">14 августа - 18 августа</span>

      <div className="schedule-section__schedule-table">
        {scheduleData.map((item, index) => (
          <Fade left>
            <div key={index} className="schedule-section__schedule-item">
              <span className="schedule-section__schedule-time">{item.date}</span>
              <div className="schedule-section__schedule-right">
                <span className="schedule-section__schedule-morning">
                  {item.time}
                </span>
                {item.schedule.map((scheduleItem, scheduleIndex) => (
                  <span
                    key={scheduleIndex}
                    className={
                      scheduleItem.includes('Послеобеденное')
                        ? 'schedule-section__schedule-position--bold'
                        : 'schedule-section__schedule-position'
                    }
                  >
                    {scheduleItem}
                  </span>
                ))}
              </div>
            </div>
          </Fade>
        ))}
      </div>

      <button className="schedule-section__download-btn" onClick={handleInstrClick}>Скачать полную программу обучения</button>

    </section>
     
  );
}

export default Schedule;
