import React from "react";
import './Reasons.scss'
import Fade from 'react-reveal/Fade';
import HeadShake from 'react-reveal/HeadShake';

function Reasons({ reasonsData }) {
  return (
    <section className="reasons-section">
      <HeadShake>
        <h1 className="reasons-section__main-title">6 причин начать обучение работе с нейросетями</h1>
        <span className="reasons-section__smal-desc">никогда не поздно начать учить что-то новое</span>
      </HeadShake>


        <div className="reasons-section__table-content">
          {reasonsData.map((reason, index) => (
            <Fade bottom>
               <div key={index} className="reasons-section__reason-item">
                <img className="reasons-section__img" src={reason.imageSrc}/>
                <div className="reasons-section__txt-block">
                  <h3 className="reasons-section__reason-title">{reason.title}</h3>
                  <span className="reasons-section__reason-text">{reason.text}</span>
                </div>
              </div>
            </Fade>
           
          ))}
        </div>
 

    </section>
  );
}

export default Reasons;
